export const useShowAnimation = (page: string) => {
  const isVistedPage = window.sessionStorage.getItem(page) != null; //  true
  if (!isVistedPage) {
    console.log(" ~");
    window.sessionStorage.setItem(page, "v");
    return false;
  }
  if (isVistedPage) {
    console.log(" ~");
    return true;
  }
  console.log("   ");
  return true;
};
